.container {
  display: flex;
  align-items: center;
  flex-direction: column;
  justify-content: center;
  margin-left: 5%;
  margin-right: 5%;
}

.title {
  font-size: 60px;
}

.created-by {
  padding: 4px;
  border-radius: 100rem;
  margin-top: 10px;
  cursor: pointer;
  font-size: 30px;
  color: rgb(44, 51, 100);
  text-align: center;
  transition: background-color 500ms linear;
}

.created-by:hover {
  font-size: 30px;
  color: #fff;
  background: rgb(2, 0, 36);
  background-color: rgb(9, 103, 5);
  text-align: center;
}

.list-about {
  list-style: none;
}

.header {
  margin-bottom: 2em;
  margin-top: 2em;
}

.list-itme {
  margin-top: 2%;
  color: rgb(26, 17, 17);
  font-size: 12px;
  padding: 15px;
  transition: background-color 250ms linear;
}

.list-itme:hover {
  margin-top: 2%;
  background-color: rgba(25, 72, 212, 0.628);
  color: #ffffff;
}

.foother {
  background-color: rgba(234, 218, 228, 0.576);
  width: 100%;
  margin-top: 2em;
  padding: 2em;
  text-align: center;
}
